import React, { Component } from "react";

class InputLineOutput extends Component {
  constructor(props) {
    super(props);
    this.state = { data: props.data };
    this.wrapper = React.createRef();
  }

  componentDidMount() {
    this.props.biRef.child.onOutputReady(this.onOutputReady);
  }

  componentWillUnmount() {}

  onOutputReady = (data) => {
    console.log("Output Line: Data Submitted :" + data);
    this.setState({
      data: data,
    });
  };

  render() {
    if (this.state.data == null) {
      return null;
    } else {
      //return <div> {this.state.data} </div>;
      return <div dangerouslySetInnerHTML={{ __html: this.state.data }} />;
    }
  }
}

export default InputLineOutput;
