import React, { Component } from "react";
import { Col, Container, Row } from "react-bootstrap";

class Hello extends Component {
  constructor(props) {
    super(props);
    console.log(props);
    this.state = {};
  }
  render() {
    return (
      <div>
        <Container fluid>
          <Row>
            <Col lg={12} className="greet">
              <span>
                /**This terminal is designed and maintained by{" "}
                <a target="_blank" href="https://github.com/sislamrafi">
                  S Islam Rafi
                </a>
                .**/
              </span>
            </Col>
            <Col lg={12} className="greet">
              <span>
                /**Frontend: react.js, Backend: laravel. library(laravel):sislamrafi/webartisan{" "}**/
              </span>
            </Col>
            <Col lg={12} className="greet">
              <span>
                /**You can write all kind of artisan commands here. (write
                without 'php artisan' prefix)**/
              </span>
            </Col>
            <Col lg={12} className="greet">
              /** example: &nbsp;
              <span>user123@terminal:~$</span>
              <span> migrate</span>
              &nbsp; **/
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Hello;
