import React, { Component } from "react";
import TerminalEnvironment from "../global/TerminalEnvironment";
import InputLine from "./line/InputLine";

class Lines extends Component {
  constructor(props) {
    super(props);
    this.lines = 0;
    this.idPrefix = "line";
    this.state = {
      lines: [this.newLine()],
    };
    TerminalEnvironment.loadTerminalEnvironmentFromHtml();
  }

  biRef = {
    parent: {
      onCommandEnd: (newMethod) => {
        this.onCommandEnd(newMethod);
      },
      clearScreen: (e) => {
        this.clearScreen(e);
      },
    },
  };

  onCommandEnd = (id) => {
    console.log("End of command. key : " + id + " :)");
    this.appendLine(id);
  };

  clearScreen = (e) => {
    this.appendLine(this.idPrefix+this.lines)
    this.setState({
      lines: this.state.lines.filter(
        (line, index) => line.props.id === this.idPrefix + this.lines
      ),
    });
  };

  newLine = () => {
    this.lines += 1;
    return (
      <InputLine
        id={this.idPrefix + this.lines}
        key={this.lines}
        biRef={this.biRef}
      />
    );
  };

  appendLine = (id) => {
    if (this.idPrefix + this.lines != id) return;
    const newData = this.newLine();
    this.setState((prevState) => ({
      lines: [...prevState.lines, newData],
    }));
  };

  render() {
    return (
      <div>
        {this.state.lines.map((line) => {
          console.log(line.props.id);
          return line;
        })}
      </div>
    );
  }
}

export default Lines;
