import React, { Component } from "react";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCopy, faTimes } from "@fortawesome/free-solid-svg-icons";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

class InputLineTail extends Component {
  constructor(props) {
    super(props);
    this.state = { timestamp: this.getTime() };
    
  }

  componentDidMount() {
    this.props.biRef.child.onEnterPress(this.onEnterPress);
  }

  onEnterPress = () => {
    console.log("Enter Press Call: InputLineTail");
    this.setState({
      timestamp: this.getTime(),
    });
  };

  getTime() {
    var currentdate = new Date();
    var datetime =
      "" +
      currentdate.getDate() +
      "/" +
      (currentdate.getMonth() + 1) +
      "/" +
      currentdate.getFullYear() +
      "@" +
      currentdate.getHours() +
      ":" +
      currentdate.getMinutes() +
      ":" +
      currentdate.getSeconds();
    return datetime;
  }

  static propTypes = {};

  copy = (e) => {
    e.preventDefault();
    this.props.biRef.parent.copyCommand();
  };

  

  render() {
    return (
      <div className="line-tail p-0">
        <span className="ml-4">{this.state.timestamp}</span>

        
          <FontAwesomeIcon onClick={this.copy} className="ml-4" icon={faCopy} />
        

        <FontAwesomeIcon className="ml-4" icon={faTimes} />
      </div>
    );
  }
}

export default InputLineTail;
