import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import InputLineHead from "./InputLineHead";
import { InputLineCommand } from "./InputLineCommand";
import InputLineTail from "./InputLineTail";
import InputLineOutput from "./InputLineOutput";
import axios from "axios";
import TerminalEnvironment from "../../global/TerminalEnvironment";

class InputLine extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.wrapper = React.createRef();
  }

  componentDidMount() {
    //this.wrapper.current.addEventListener("keydown", this._handleKeyDownEvent);
    //this.wrapper.current.focus();
    //this.wrapper.current.setSelectionRange(5, 5);

    //this.focusAndMoveCursorToTheEnd(this.wrapper.current);

    //console.log(this.wrapper.current.innerText.length);
    this.wrapper.current.addEventListener("click", this.getFocusOnInput);
  }

  componentWillUnmount() {
    //this.wrapper.current.removeEventListener(
    //  "keydown",
    //  this._handleKeyDownEvent
    //);
    this.wrapper.current.removeEventListener("click", this.getFocusOnInput);
  }

  submit = (command) => {
    console.log("submit command form child :" + command);
    if (command == "cls") {
      this.props.biRef.parent.clearScreen(command);
      return;
    }
    //do operations

    //update childs
    this.onEnterPress();

    //request api
    this.apiRequest(command);

    //on command end
  };

  apiRequest = (command) => {
    var csrf = document.querySelector('meta[name="csrf-token"]')["content"];
    var route = document.querySelector('meta[name="submit_route"]')["content"];
    console.log(csrf + " " + route);
    const packets = {
      command: command,
      _token: csrf,
    };
    const headers = {
      "Access-Control-Allow-Origin": "*",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
    };

    axios
      .request({
        url: route,
        method: "post",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
        },
        data: packets,
      })
      .then((response) => {
        console.log(response.data);
        this.afterResponseFromServer(response.data);
      })
      .catch((error) => {
        console.log("ERROR:: ", error);
        this.afterResponseFromServer(error.toString());
      });
    /*
    axios
      .post(route, packets, headers)
      .then((response) => {
        console.log(response.data);
        this.afterResponseFromServer(response.data);
      })
      .catch((error) => {
        console.log("ERROR:: ", error);
        this.afterResponseFromServer(error.toString());
      });
      */
  };

  afterResponseFromServer = (data) => {
    try {
      var returnObject = data;
      TerminalEnvironment.loadTerminalEnvironment(
        returnObject.user,
        returnObject.environment
      );
      this.onOutputReady(returnObject.html);
      this.props.biRef.parent.onCommandEnd(this.props.id);
      return;
    } catch (err) {
      this.onOutputReady(err);
      this.props.biRef.parent.onCommandEnd(this.props.id);
      return;
    }
  };

  biRef = {
    parent: {
      onEnterPress: (newMethod) => {
        this.submit(newMethod);
      },
      copyCommand: (newMethod) => {
        this.copy();
      },
    },
    child: {
      command: "",

      clickOutside: (newMethod) => {
        this.getFocusOnInput = newMethod;
      },
      onOutputReady: (newMethod) => {
        this.onOutputReady = newMethod;
      },
      onEnterPress: (newMethod) => {
        this.onEnterPress = newMethod;
      },
      onCopyRequest: (newMethod) => {
        this.copy = newMethod;
      },
    },
  };

  copy = () => {};

  getFocusOnInput = () => {};

  onOutputReady = (data) => {};

  onEnterPress = () => {};

  /*handleClickOutside = (newMethod) => {
    this.getFocusOnInput = newMethod;
  };*/

  render() {
    return (
      <div ref={this.wrapper} className="line" spellCheck="false">
        <Container fluid>
          <Row>
            <Col xs={12} sm={12} md={9} lg={10}>
              <InputLineHead user={TerminalEnvironment.user.username} platform={TerminalEnvironment.terminal.name} />
              <InputLineCommand
                biRef={
                  this.biRef
                } /*handleClickOutside={this.handleClickOutside}*/
              />
            </Col>
            <Col
              className="d-none d-md-block"
              lg={2}
              md={3}
              style={{ alignItems: "right" }}
            >
              <InputLineTail biRef={this.biRef} />
            </Col>
            <Col>
              <InputLineOutput data={null} biRef={this.biRef} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default InputLine;
