import React, { Component } from "react";

export class InputLineCommand extends Component {
  constructor(props) {
    super(props);
    this.wrapper = React.createRef();

    //this.focus = this.focus.bind(this);
  }

  componentDidMount() {
    this.focusAndMoveCursorToTheEnd();
    this.wrapper.current.addEventListener("keydown", this.onKeyDown);
    //this.props.handleClickOutside(this.handleClickOutside);
    this.props.biRef.child.clickOutside(this.handleClickOutside);
    this.props.biRef.child.onCopyRequest(this.onCopyRequest);
  }

  componentWillUnmount() {
    this.wrapper.current.removeEventListener("keydown", this.onKeyDown);
  }

  onKeyDown = (event) => {
    //console.log("keyboard event "+event.key);
    if (event.key == "Enter") {
      event.preventDefault();
      this.props.biRef.parent.onEnterPress(this.wrapper.current.innerText);
    }
  };

  handleClickOutside = (event) => {
    if (this.wrapper && !this.wrapper.current.contains(event.target)) {
      console.log("clicked outside me");
      //console.log(event.target);
      //console.log(this.wrapper.current);
      event.preventDefault();
      this.focusAndMoveCursorToTheEnd();
    }
  };

  onCopyRequest = (e) =>{
    console.log("Copy: "+this.wrapper.current.innerText);
    navigator.clipboard.writeText(this.wrapper.current.innerText);
  }

  focusAndMoveCursorToTheEnd() {
    var input = this.wrapper.current;
    input.focus();

    const range = document.createRange();
    const selection = window.getSelection();
    const { childNodes } = input;
    const lastChildNode = childNodes && childNodes.length - 1;

    range.selectNodeContents(
      lastChildNode === -1 ? input : childNodes[lastChildNode]
    );
    range.collapse(false);

    selection.removeAllRanges();
    selection.addRange(range);
  }

  /*focus = () => {
    this.wrapper.current.focus();
    console.log("called method from child");
  };*/
  render() {
    return (
      <div
        ref={this.wrapper}
        className="line-command"
        contentEditable="true"
        suppressContentEditableWarning={true}
      ></div>
    );
  }
}

export default InputLineCommand;
