class TerminalEnvironment {
  static user = {
      username : null,
  };
  static terminal = {
    name : null,
  };

  constructor() {
  }

  static loadTerminalEnvironmentFromHtml(){
      this.user = window.user;
      this.terminal = window.environment;
  }

  static loadTerminalEnvironment(user, terminal) {
      this.user = user;
      this.terminal = terminal;
  }
}
 
export default TerminalEnvironment;